<template>
  <div class="orderDetail mescroll">
    <div class="address flex" v-if="detail.orderType != 7">
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">收货人：{{detail.userName2}}</span>
          <span class="tenlo">{{detail.userMobile}}</span>
        </div>
        <div class="addressInfo">{{detail.addrAddr}}</div>
      </div>
    </div>
    <div class="address flex" v-else>
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">提货人：{{detail.userName2}}</span>
          <span class="tenlo">{{detail.userMobile}}</span>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="goodsInfo">
      <div class="goodsInfo-top flex">
        <div class="flex-2 title">商品信息</div>
        <div class="status" :style="detail.orderStatus == 1 || detail.orderStatus == 3?'color:#eb5252':detail.orderStatus == 4?'color:#333':'color:#999'">{{$utils.getText(detail.orderStatus)}}</div>
      </div>
      <div class="list">
        <div class="item flex" v-for="(item,index) in detail.scEcomOrdersDetailList" :key="index">
          <div class="item-pic flex-1">
            <img :src="$utils.dealImg(item.scEcomGoods.gimg)" />
          </div>
          <div class="flex-2">
            <div class="title">{{item.scEcomGoods.gname}}</div>
            <div class="price">
              ¥
              <span class="money">{{item.scEcomGoods.goutPrice?item.scEcomGoods.goutPrice.toFixed(2):'0.00'}}</span>
              <span class="num">×{{item.gOrderCount}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="goodsInfo-bottom">
        <div class="flex" v-if="false">
          <div class="flex-1" style="color:#333">使用卡券</div>
          <div class="flex-2">否</div>
        </div>
        <div class="flex1" v-if="detail.remarks">
          <div class="flex-1">备注</div>
          <div class="flex-2">{{detail.remarks}}</div>
        </div>
        <div class="line1"></div>
        <div class="flex">
          <div class="flex-1">商品数量</div>
          <div class="flex-2">{{detail.shopCount}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">商品合计</div>
          <div class="flex-2">￥{{detail.oOrderMoney?detail.oOrderMoney.toFixed(2):'0.00'}}</div>
        </div>
        <div class="flex" v-if="detail.oOrderYunfei">
          <div class="flex-1">运费合计</div>
          <div class="flex-2">￥{{detail.oOrderYunfei.toFixed(2)}}</div>
        </div>
        <div class="flex" v-if="detail.oOrderYouhui">
          <div class="flex-1">优惠合计</div>
          <div class="flex-2">￥{{detail.oOrderYouhui.toFixed(2)}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">实付款</div>
          <div class="flex-2 allPrice">
            ￥
            <span>{{detail.oOrderMoneyShifu?detail.oOrderMoneyShifu.toFixed(2):'0.00'}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="orderInfo" :style="flag == 0?'padding-bottom:70px':''">
      <!-- :style="detail.orderStatus  == 1 || detail.orderStatus  == 3 || detail.orderStatus  == 5?'padding-bottom:70px;':''" -->
      <div class="title">订单信息</div>
      <div class="flex">
        <div class="flex-1">订单编号</div>
        <div class="flex-2">{{detail.orderno}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">下单时间</div>
        <div class="flex-2">{{detail.oOrderWdate}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 9">
        <div class="flex-1">支付方式</div>
        <div class="flex-2">{{detail.pay_type == 1?'微信':(detail.pay_type == 2?'微信' : '支付宝')}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 9">
        <div class="flex-1">支付时间</div>
        <div class="flex-2">{{detail.oPayWdate}}</div>
      </div>
      <div class="flex" v-if="type != '1' && closeDetail.shopName">
        <div class="flex-1">核销商户</div>
        <div class="flex-2">{{closeDetail.shopName}}</div>
      </div>
      <div class="flex" v-if="type != '1' &&　closeDetail.userName2">
        <div class="flex-1">核销人</div>
        <div class="flex-2">{{closeDetail.userName2}}</div>
      </div>
      <div class="flex" v-if="type != '1'　&& closeDetail.createTime">
        <div class="flex-1">核销时间</div>
        <div class="flex-2">{{closeDetail.createTime}}</div>
      </div>
    </div>
    <div class="bottom" v-if="flag == 0">
      <div class="btns flex">
        <div class="btn btnPay" v-if="flag == 0" @click="handle(1)">核销订单</div>
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from "vant";
import {
  beforeCheckOutOrderDetail,
  afterCheckOutOrderDetail,
  checkOutOrder,
} from "@/api";
export default {
  data() {
    return {
      hour: 2,
      min: 1,
      detail: {},
      closeDetail: {},
      type: "",
      flag: null,
    };
  },
  methods: {
    handle(type) {
      let that = this;
      switch (type) {
        case 1:
          //核销订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定核销此订单？",
          })
            .then(() => {
              // on confirm
              that.submit();
            })
            .catch(() => {
              // on cancel
            });
          break;
        default:
          console.log(111);
          break;
      }
    },
    submit() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      checkOutOrder({
        orderCode: this.detail.orderCode,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("核销成功");
          this.type = "2";
          this.loadData();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      if (this.type == "1") {
        beforeCheckOutOrderDetail({
          orderCode: this.$route.query.id,
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            if (res.data.flag != 0) {
              this.$toast(res.data.message);
            }
            this.flag = res.data.flag;
            this.detail = res.data.scEcomOrders;
            this.detail.addrText =
              this.detail.addr.area.treeNames.replace(/\//g, "") +
              this.detail.addr.addrAddr;
          } else {
            this.$toast(res.msg);
          }
        });
      } else {
        afterCheckOutOrderDetail({
          orderCode: this.$route.query.orderCode,
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.flag = 1;
            this.detail = res.data.scEcomOrders;
            this.closeDetail = res.data.scEcomOrdersEmp;
          } else {
            this.$toast(res.msg);
          }
        });
      }
    },
  },
  mounted() {
    document.title = "核销订单详情";
    this.type = this.$route.query.type || "2";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.orderDetail {
  .line {
    height: 5px;
    background: #efefef;
  }
  .line1 {
    border-top: 1pt solid #efefef;
    margin: 20px 0 5px;
  }
  .flex {
    display: flex;
    align-items: center;
    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      position: relative;
      height: 100%;
    }
  }

  .address {
    padding: 15px;
    .icon {
      width: 20px;
      margin-right: 10px;
    }

    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      font-size: 15px;
    }

    .personalInfo {
      font-size: 16px;
      line-height: 15px;
      font-weight: bold;
      .name {
        margin-right: 10px;
      }
      .tenlo {
        float: right;
      }
    }
    .addressInfo {
      font-size: 14px;
      color: #999;
      margin-top: 10px;
      line-height: 20px;
      .arrow {
        margin-top: 10px;
      }
    }
  }
  .goodsInfo {
    padding: 0 15px;
    margin-top: 10px;
    .goodsInfo-top {
      align-items: center;
      .title {
        font-size: 18px;
        font-weight: bold;
        line-height: 25px;
      }
      .status {
        font-size: 15px;
        line-height: 25px;
        color: $base-color;
      }
    }
    .list {
      .item {
        height: 120px;
        overflow: hidden;
        margin: 20px 0;
        align-items: flex-start;
        .item-pic {
          margin-right: 10px;
          img {
            width: 100%;
            height: 120px;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 10px 0;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
            color: #999;
            margin-left: 10px;
          }
        }
      }
    }
    .goodsInfo-bottom {
      padding: 10px 0;
      .flex {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        align-items: center;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .flex1 {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .allPrice {
        text-align: right;
        font-size: 20px;
        font-weight: bold;
        span {
          font-size: 30px;
          font-weight: bold;
        }
      }
    }
  }
  .orderInfo {
    padding: 10px 15px 20px;
    .title {
      font-size: 18px;
      font-weight: bold;
      line-height: 25px;
      margin: 0 0 10px;
    }
    .flex {
      line-height: 30px;
      color: #999;
      font-size: 16px;
      display: flex;
      .flex-1 {
        flex: 1;
      }
      .flex-2 {
        text-align: right;
        color: #333;
        flex: 2;
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 50px;
    background: #fff;
    width: 100%;
    box-shadow: 0 -2px 10px 1px rgba(0, 0, 0, 0.1);
    padding: 5px 0;
    .btns {
      padding: 0 15px;
      justify-content: flex-end;
      margin-top: 9px;
      .btn {
        width: 100px;
        color: #666;
        border: 1pt solid #f4f4f4;
        float: right;
        height: 30px;
        line-height: 30px;
        border-radius: 5px;
        font-size: 13px;
        text-align: center;
        margin-left: 10px;
      }
      .btnPay {
        background: $base-color;
        color: #fff;
        border: 1pt solid $base-color;
      }
    }
  }
}
</style>
